import React, { useEffect, useState } from "react";
import axios from "../../../axios";
import { css } from "@emotion/core";
import BeatLoader from "react-spinners/BeatLoader";
import { useDispatch } from "react-redux";
import { Formik, Form, Field } from "formik";
import {
  Avatar,
  Button,
  CssBaseline,
  Link,
  Grid,
  Box,
  Typography,
  Container,
} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import { TextField } from "formik-material-ui";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import { makeStyles } from "@material-ui/core/styles";

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href="https://gdcpulwama.edu.in/">
        Romush Technologies
      </Link>
       <span>-</span>{new Date().getFullYear()}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  textbox: {
    backgroundColor: "#fff",
  },
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: "20px",
    backgroundColor: "#0a4372",
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(2, 0, 2),
    padding: "10px",
    backgroundColor: "#0a4372",
  },
}));
export default function SignIn() {
  const classes = useStyles();
  const override = css`
    display: block;
    margin: 0 auto;
    border-color: blue;
  `;
  const [loadding, setloadding] = useState(false);
  const [checkid, setcheckid] = useState(false);
  const [errordesc, setErrordesc] = useState("");
  const dispatch = useDispatch();
  useEffect(() => {
    var token = sessionStorage.getItem("token");
    var userAuth = sessionStorage.getItem("userAuth");
    var id = sessionStorage.getItem("id");
    var username = sessionStorage.getItem("username");
    var user_roll = sessionStorage.getItem("user_roll");
    var Dept_ID = sessionStorage.getItem("Dept_ID");
    var email = sessionStorage.getItem("email");
    var collegeID = sessionStorage.getItem("collegeID");
    var firstName = sessionStorage.getItem("firstName");
    var middleName = sessionStorage.getItem("middleName");
    var lastName = sessionStorage.getItem("lastName");
    var phone_no = sessionStorage.getItem("phone_no");
    var college_name = sessionStorage.getItem("college_name");
    var Dept_Name = sessionStorage.getItem("Dept_Name");
    if (userAuth == "true" && token) {
      setSignIn(
        id,
        username,
        Dept_ID,
        email,
        collegeID,
        firstName,
        middleName,
        lastName,
        phone_no,
        college_name,
        token,
        userAuth,
        Dept_Name,
        user_roll
      );
      document.location = "#/dashboard";
    }
  }, []);
  const setSignIn = (
    id,
    username,
    Dept_ID,
    email,
    collegeID,
    firstName,
    middleName,
    lastName,
    phone_no,
    college_name,
    token,
    userAuth,
    Dept_Name,
    user_roll
  ) => {
    dispatch({
      type: "login",
      isLoggedIn: userAuth,
      id: id,
      username: username,
      Dept_ID: Dept_ID,
      email: email,
      collegeID: collegeID,
      firstName: firstName,
      middleName: middleName,
      lastName: lastName,
      phone_no: phone_no,
      college_name: college_name,
      token: token,
      Dept_Name: Dept_Name,
      user_roll: user_roll,
    });
  };
  return (
    <div
      style={{
        backgroundImage: "url(Images/background.jpg)",
        height: "100vh",
        paddingTop: "10px",
      }}
    >
      <Container
        component="main"
        maxWidth="xs"
        style={{
          backgroundColor: "#fff",
          boxShadow:
            "rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px",

          paddingBottom: "30px",
        }}
      >
        <CssBaseline />
        <div className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            <span style={{ color: "#0a4372" }}>College ERP Admin Portal</span>
          </Typography>
          {checkid ? (
            <Alert severity="error">
              Invalid User Name or Password!
              <br />
              {errordesc}
            </Alert>
          ) : (
            <div class="formspacer " />
          )}
          <Formik
            initialValues={{
              username: "",
              password: "",
            }}
            validate={(values) => {
              const errors = {};
              if (!values.username) {
                errors.username = "Username Required";
              }
              if (!values.password) {
                errors.password = "Password Required";
              }

              return errors;
            }}
            onSubmit={(values, { setSubmitting }) => {
              setTimeout(() => {
                setSubmitting(true);
                setloadding(true);
                setcheckid(false);
                //alert(JSON.stringify(values, null, 2));
                axios
                  .post("login", JSON.stringify(values), {
                    headers: {
                      // Overwrite Axios's automatically set Content-Type
                      "Content-Type": "application/json",
                      Accept: "application/json",
                    },
                  })
                  .then((res) => {
                    if (res.status === 200 && res.data.login === "success") {
                      sessionStorage.setItem("userAuth", res.data.userAuth);
                      sessionStorage.setItem("token", res.data.token);
                      sessionStorage.setItem("id", res.data.results[0].id);
                      sessionStorage.setItem(
                        "user_roll",
                        res.data.results[0].user_roll
                      );
                      sessionStorage.setItem(
                        "Dept_Name",
                        res.data.results[0].Department_Name
                      );
                      sessionStorage.setItem(
                        "Dept_ID",
                        res.data.results[0].Dept_ID
                      );
                      sessionStorage.setItem(
                        "username",
                        res.data.results[0].username
                      );
                      sessionStorage.setItem(
                        "email",
                        res.data.results[0].email
                      );
                      sessionStorage.setItem(
                        "collegeID",
                        res.data.results[0].collegeID
                      );
                      sessionStorage.setItem(
                        "firstName",
                        res.data.results[0].firstName
                      );
                      sessionStorage.setItem(
                        "middleName",
                        res.data.results[0].middleName
                      );
                      sessionStorage.setItem(
                        "lastName",
                        res.data.results[0].lastName
                      );
                      sessionStorage.setItem(
                        "phone_no",
                        res.data.results[0].phone_no
                      );
                      sessionStorage.setItem(
                        "college_name",
                        res.data.results[0].college_name
                      );
                      var userAuth = "true";
                      setSignIn(
                        res.data.results[0].id,
                        res.data.results[0].username,
                        res.data.results[0].Dept_ID,
                        res.data.results[0].email,
                        res.data.results[0].collegeID,
                        res.data.results[0].firstName,
                        res.data.results[0].middleName,
                        res.data.results[0].lastName,
                        res.data.results[0].phone_no,
                        res.data.results[0].college_name,
                        res.data.token,
                        userAuth,
                        res.data.results[0].Department_Name,
                        res.data.results[0].user_roll
                      );
                      document.location = "#/dashboard";
                      setloadding(false);
                      setSubmitting(false);
                    }
                  })
                  .catch(function (error) {
                    setloadding(false);
                    setSubmitting(false);
                    setcheckid(true);
                    setErrordesc(error.message);
                  });
              }, 500);
            }}
          >
            {({ submitForm, isSubmitting }) => (
              <Form className={classes.form}>
                <Field
                  className={classes.textbox}
                  component={TextField}
                  variant="outlined"
                  margin="normal"
                  required
                  fullWidth
                  id="username"
                  label="User Name"
                  name="username"
                  autoComplete="username"
                  autoFocus
                />
                <Field
                  className={classes.textbox}
                  component={TextField}
                  variant="outlined"
                  margin="normal"
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                />

                <div
                  className="text-center"
                  style={{ "margin-top": "50px", "margin-bottom": "40px" }}
                >
                  <BeatLoader
                    css={override}
                    size={15}
                    margin={2}
                    color={"#154883"}
                    loading={loadding}
                  />
                </div>
                {!loadding ? (
                  <Button
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.submit}
                    disabled={isSubmitting}
                    onClick={submitForm}
                  >
                    Sign In
                  </Button>
                ) : null}
              </Form>
            )}
          </Formik>
          <Grid container>
            <Grid item xs>
              <Link href="#" variant="body2" style={{ color: "#0a4372" }}>
                Forgot password?
              </Link>
            </Grid>
            <Grid item>
              <Link href="#" variant="body2" style={{ color: "#0a4372" }}>
                {"For Help Call: +91 9906042610"}
              </Link>
            </Grid>
          </Grid>
        </div>
        <Box mt={8}>
          <Copyright />
        </Box>
      </Container>
    </div>
  );
}
